___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
button,
textarea,
select {
  margin: 0;
  font: inherit;
}

:root {
  --font-family-main: consolas, "Times New Roman", serif;
  --font-size-m: 16px;
  --font-line-m: 24px;
  --font-m: var(--font-size-m) / var(--font-line-m) var(--font-family-main);
  --font-size-l: 24px;
  --font-line-l: 32px;
  --font-l: var(--font-size-l) / var(--font-line-l) var(--font-family-main);
  --font-size-xl: 32px;
  --font-line-xl: 40px;
  --font-xl: var(--font-size-xl) / var(--font-line-xl) var(--font-family-main);
  --navbar-height: 50px;
  --sidebar-width: 300px;
  --sidebar-width-collapsed: 80px;
  --modal-z-index: 10;
  --overlay-color: rgba(0 0 0 / 60%);
  --red-light: red;
  --red-dark:red;
}

.app_light_theme {
  --bg-color: #fff3ff;
  --inverted-bg-color: #32323a;
  --primary-color: #470050;
  --secondary-color: #c622d4;
  --inverted-primary-color: #c20288;
  --inverted-secondary-color: #ff0479;
}

.app_dark_theme {
  --bg-color: #32323a;
  --inverted-bg-color: #fff3ff;
  --primary-color: #c695fd;
  --secondary-color: #8c6494;
  --inverted-primary-color: #c20288;
  --inverted-secondary-color: #ff0479;
}

.body {
  color: var(--primary-color);
  font: var(--font-m);
  min-height: 100vh;
}

.app {
  background: var(--bg-color);
}

.content-page {
  display: flex;
}

.page-wrapper {
  flex-grow: 1;
  padding: 20px;
}`, "",{"version":3,"sources":["webpack://./src/app/styles/reset.scss","webpack://./src/app/styles/index.scss","webpack://./src/app/styles/variables/global.scss","webpack://./src/app/styles/themes/normal.scss","webpack://./src/app/styles/themes/dark.scss"],"names":[],"mappings":"AAAA;EACI,SAAA;EACA,UAAA;EACA,sBAAA;ACCJ;;ADEA;;;;EAII,SAAA;EAEA,aAAA;ACAJ;;ACZA;EACI,sDAAA;EACA,mBAAA;EACA,mBAAA;EACA,yEAAA;EACA,mBAAA;EACA,mBAAA;EACA,yEAAA;EACA,oBAAA;EACA,oBAAA;EACA,4EAAA;EAGA,qBAAA;EACA,sBAAA;EACA,+BAAA;EAGA,mBAAA;EAGA,kCAAA;EACA,gBAAA;EACA,cAAA;ADSJ;;AEhCA;EACI,mBAAA;EACA,4BAAA;EAEA,wBAAA;EACA,0BAAA;EAEA,iCAAA;EACA,mCAAA;AFiCJ;;AGzCA;EACI,mBAAA;EACA,4BAAA;EAEA,wBAAA;EACA,0BAAA;EAEA,iCAAA;EACA,mCAAA;AH0CJ;;AA5CA;EACI,2BAAA;EACA,mBAAA;EACA,iBAAA;AA+CJ;;AA7CA;EAEI,2BAAA;AA+CJ;;AA5CA;EACI,aAAA;AA+CJ;;AA5CA;EACI,YAAA;EACA,aAAA;AA+CJ","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\ninput,\r\nbutton,\r\ntextarea,\r\nselect {\r\n    margin: 0;\r\n\r\n    font: inherit;\r\n}\r\n","@import \"reset\";\n@import \"variables/global\";\n@import \"themes/normal\";\n@import \"themes/dark\";\n\n\n.body {\n    color: var(--primary-color);\n    font: var(--font-m);\n    min-height: 100vh;\n}\n.app {\n\n    background: var(--bg-color);\n\n}\n.content-page {\n    display: flex;\n}\n\n.page-wrapper {\n    flex-grow: 1;\n    padding: 20px;\n}\n",":root {\r\n    --font-family-main: consolas, \"Times New Roman\", serif;\r\n    --font-size-m: 16px;\r\n    --font-line-m: 24px;\r\n    --font-m: var(--font-size-m) / var(--font-line-m) var(--font-family-main);\r\n    --font-size-l: 24px;\r\n    --font-line-l: 32px;\r\n    --font-l: var(--font-size-l) / var(--font-line-l) var(--font-family-main);\r\n    --font-size-xl: 32px;\r\n    --font-line-xl: 40px;\r\n    --font-xl: var(--font-size-xl) / var(--font-line-xl) var(--font-family-main);\r\n\r\n    // Размеры\r\n    --navbar-height: 50px;\r\n    --sidebar-width: 300px;\r\n    --sidebar-width-collap